.menuItem {
  position: relative;
  display: inline-block;
  padding: 0 50px;
  border-right: 1px solid rgba(178, 178, 178, 0.5);
  line-height: 50px; }
  .menuItem:first-child {
    border-left: 1px solid rgba(178, 178, 178, 0.5); }
  .menuItem:hover {
    background-color: rgba(203, 203, 203, 0.5);
    cursor: pointer; }
  .menuItem a {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    color: #646464;
    font-size: 18px; }
